import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { AngularFireDatabase, AngularFireList, AngularFireObject } from 'angularfire2/database';
import * as firebase from 'firebase';
import { IAccount } from './../../../declarations';
import { IPost } from './../../../declarations';


@Injectable()
export class FirebaseService {
  users: Observable<any>;
  posts: AngularFireList<any>;
  post: Observable<IPost>;
  folder: any;

  constructor(private db: AngularFireDatabase) {
    this.folder = 'usersavatars';
  }

  getUsers() {
    this.users = this.db.list('/users').valueChanges();
    return this.users;
  }

  getPosts() {
    this.posts = this.db.list('/posts');
    return this.posts;
  }

  getPost(id: string) {
    this.post = this.db.object<IPost>(`/posts/${id}`).valueChanges();
    return this.post;
  }

  addPost(newPost: IPost) {
    this.db.list('/posts').push(newPost);
  }

  updatePost(id: string, el: any) {
    this.db.object<IPost>(`/posts/${id}`).update(el);
  }

  deletePost(key: string) {
    this.db.list('/posts').remove(key);
  }

  addAccount(newAccount: IAccount) {
    if (newAccount.avatar !== '') {
      const storageRef = firebase.storage().ref();
      const path = `/${this.folder}/${newAccount.avatar.name}`
      const iRef = storageRef.child(path)
      iRef.put(newAccount.avatar).then( (snapshot) => {
        newAccount.avatar = path;
        return this.db.list('/users').push(newAccount);
      })
    } else {
      this.db.list('/users').push(newAccount);
    }
  }

}



