import { Component, OnInit } from '@angular/core';
import { FirebaseService } from './../../shared/services'
import { ActivatedRoute, Router } from '@angular/router';
import { IPost, Posts } from './../../declarations';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.scss']
})
export class PostComponent implements OnInit {
  post: IPost = {
    author: {
      nickname: '',
      avatar: '',
    },
    title: '',
    text: '',
    comments: []
  }
  edit = false;
  postId: string;
  fieldEdit = {
    title: false,
    text: false
  };
  editFormGroup = new FormGroup({
    title: new FormControl(''),
    text: new FormControl('')
  });

  constructor(private activatedRoute: ActivatedRoute,
              private router: Router,
              private firebaseService: FirebaseService) {
      this.postId = activatedRoute.snapshot.params.id;
    }

  ngOnInit() {
    if (this.activatedRoute.snapshot && this.activatedRoute.snapshot.data) {
      this.edit = this.activatedRoute.snapshot.data.edit;
    }
    this.firebaseService.getPost(this.postId).subscribe( (res) => this.post = res)
  }

  deletePost(id: string) {
    this.firebaseService.deletePost(id);
    this.router.navigate(['admin/posts']);
  }

  editField(type: string, field: string) {
    this.editFormGroup.patchValue({
      [type]: field
    });
    this.fieldEdit[type] = true;
  }

  saveField(type: string) {
    this.firebaseService.updatePost(this.postId, {
      [type]: this.editFormGroup.get(type).value
    });
    this.fieldEdit[type] = false;
  }

  cancel(type: string) {
    this.fieldEdit[type] = false;
  }

}



