import { Injectable } from '@angular/core';
import { FirebaseService } from '../firebase/firebase.service'
import { IAccount } from './../../../declarations';
import * as firebase from 'firebase';

@Injectable()
export class AuthService {
  private _account: IAccount;
  private _isLoggedIn: boolean;
  private _isLoginError: boolean;

  constructor(private firebaseService: FirebaseService) {
    this.checkPermission(localStorage.getItem('role'));
  }

  login({email, password}): void {
    this.firebaseService
      .getUsers()
      .subscribe( (users) => {
        this._account = users.filter( (user) => email === user.email && password === user.password)[0]
        if (this._account !== undefined) {
          this.checkPermission(this._account.role);
          this._isLoginError = false;
          localStorage.setItem('role', this._account.role);
          firebase.storage().ref().child(this._account.avatar).getDownloadURL().then( (url) => {
            localStorage.setItem('user', JSON.stringify([{
              avatar: url,
              email: this._account.email,
              nickname: this._account.nickname,
            }]));
          })
        } else {
          this._isLoginError = true;
        }
      })
  }

  get loggedUser() {
    return JSON.parse(localStorage.getItem('user'))[0];
  }

  checkPermission(role) {
    this._isLoggedIn = (role === 'ROLE_ADMIN');
  }

  loginError() {
    return this._isLoginError;
  }

  logOut(): void {
    this._isLoggedIn = false;
    localStorage.setItem('role', '');
    localStorage.setItem('user', '');
  }

  get isLoggedIn() {
    return this._isLoggedIn;
  }

}



