import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {
  HomeComponent,
  NotFoundComponent,
  PostComponent,
  PostsComponent,
  PostsListComponent,
  AuthorizationComponent
} from './components';

import { AuthAdminGuard } from './modules/admin/guards/auth-admin.guard';

const msRoutes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'posts', component: PostsComponent,
    children: [
      { path: '', component: PostsListComponent },
      { path: ':id', component: PostComponent },
    ]
  },
  { path: 'registration', data: { registration: true }, component: AuthorizationComponent },
  { path: 'login', data: { registration: false }, component: AuthorizationComponent },
  {
    path: 'admin', loadChildren: './modules/admin/admin.module#AdminModule', canActivate: [AuthAdminGuard]
  },
  { path: '404', component: NotFoundComponent },
  { path: '**', redirectTo: '/404' }
];

@NgModule({
  imports: [
    RouterModule.forRoot(msRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule {}

export const routingComponents = [
  HomeComponent,
  NotFoundComponent,
  AuthorizationComponent
];



