export const regFormFields = [
  {
    name: 'nickname',
    placeholder: 'Enter your nickname'
  },
  {
    name: 'email',
    placeholder: 'Enter your email'
  },
  {
    name: 'password',
    placeholder: 'Enter your password',
    type: 'password'
  }
];

export const patterns = {
  patternNickname: /^[A-Za-z\-\s]*$/,
  patternPassword: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
}



