import { Component, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { IPost, Posts } from './../../declarations';
import { FirebaseService } from '../../shared/services'
import { AuthService } from '../../shared/services'
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-popup',
  templateUrl: './popup.component.html',
  styleUrls: ['./popup.component.scss']
})
export class PopupComponent {
  post: IPost;
  postFormGroup = new FormGroup({
    title: new FormControl('', [
      Validators.required,
      Validators.minLength(2)
    ]),
    text: new FormControl('', [
      Validators.required,
      Validators.minLength(2)
    ]),
  });
  private title = this.postFormGroup.get('title');
  private text = this.postFormGroup.get('text');

  constructor(
    private firebaseService: FirebaseService,
    private authService: AuthService,
    public dialogRef: MatDialogRef<PopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
    this.dialogRef.close();
  }

  getErrorMessage(fieldName: string) {
    switch (fieldName) {
      case fieldName:
        return this[fieldName].hasError('required') ? 'You must enter a value' :
               this[fieldName].hasError('minlength') ? 'Minimun 2 characters' : '';
      default:
        return '';
    }
  }

  addPost() {
    if (this.isPostFieldsValid()) {
      this.post = {
        title: this.title.value,
        text: this.text.value,
        author: {
          nickname: this.authService.loggedUser.nickname,
          avatar: this.authService.loggedUser.avatar,
        },
        comments: []
      }
      this.firebaseService.addPost(this.post);
      this.dialogRef.close();
    }
  }

  private isPostFieldsValid() {
    return this.title.valid &&
           this.text.valid;
  }

}



